import React, { useState } from 'react';
import * as Yup from 'yup';
import { Formik, Form } from 'formik';
import { motion, AnimatePresence } from 'framer-motion';
import styles from './contact.module.scss';
import FormElm from '../../components/formElm/FormElm';
import {
  pageAnimation,
  submitBtnAnimation,
  successAnimation,
} from '../../animations/animation';

const initialValues = {
  email: '',
  mobile: '',
  desc: '',
};
const validationSchema = Yup.object({
  email: Yup.string().required('email is required').email('enter valid email'),
  mobile: Yup.string()
    .required('mobile is required')
    .matches(/^09\d{9}$/, 'enter valid mobile'),
  desc: Yup.string().max(100, 'description should be less than 100 characters'),
});

const Contact = () => {
  const [showSuccess, setShowSuccess] = useState(false);
  const onSubmit = async (values, submitProps) => {
    // only enter to onSubmit if we dont have any error
    setTimeout(() => {
      submitProps.setSubmitting(false);
      submitProps.resetForm();
      setShowSuccess(true);
      setTimeout(() => setShowSuccess(false), 2000);
    }, 1000);
  };
  return (
    <motion.div
      variants={pageAnimation}
      initial='initial'
      animate='animate'
      exit='exit'
    >
      <div className={styles.container}>
        <h6 className={styles.title}> contact form </h6>
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={onSubmit}
          validateOnMount={false}
          validateOnChange
          validateOnBlur
        >
          {formik => {
            return (
              <Form noValidate className={styles.form}>
                <FormElm
                  tag='input'
                  type='email'
                  id='email'
                  name='email'
                  placeholder='enter your email'
                  label='enter email :'
                  haveError={formik.touched.email && formik.errors.email}
                />
                <FormElm
                  tag='input'
                  type='tel'
                  id='mobile'
                  name='mobile'
                  placeholder='enter your mobile'
                  label='enter mobile :'
                  haveError={formik.touched.mobile && formik.errors.mobile}
                />
                <FormElm
                  tag='textarea'
                  id='desc'
                  name='desc'
                  placeholder='enter description'
                  label='enter description :'
                  haveError={formik.touched.desc && formik.errors.desc}
                />
                <motion.button
                  type='submit'
                  className={styles.submit}
                  disabled={
                    formik.isSubmitting || (formik.dirty && !formik.isValid)
                  }
                  variants={submitBtnAnimation}
                  initial='initial'
                  animate='animate'
                  whileHover='whileHover'
                  whileTap='whileTap'
                >
                  submit form
                </motion.button>
                <AnimatePresence>
                  {showSuccess && (
                    <motion.p
                      className={styles.success}
                      variants={successAnimation}
                      initial='initial'
                      animate='animate'
                      exit='exit'
                    >
                      form submitted successfully
                    </motion.p>
                  )}
                </AnimatePresence>
              </Form>
            );
          }}
        </Formik>
      </div>
    </motion.div>
  );
};
export default Contact;
